@use "styles/stylesFunctions.scss" as f;
@use "styles/sassVariables" as v;
.row-styles {
  align-items: center;
  padding-top: 44px;
  padding-bottom: 151px;
  .header {
    margin-bottom: 24px;
    @include f.fontStyles(36px, 38px, 400px, #000, "Tajawal");
  }
  p {
    margin: 0;
    margin-right: 30px;
  }

  .section {
    margin-bottom: 35px;
    h5 {
      @include f.fontStyles(25px, 16px, 800px, #000, "Tajawal");
      font-weight: bolder;
      margin-right: 30px;
    }
    p {
      max-width: 65%;
    }
    .flex {
      display: flex;
      gap: 0 15px;

      max-width: 524px;

      h4 {
        @include f.fontStyles(25px, 16px, 800px, #000, "Tajawal");
        font-weight: bolder;
        margin: 0;
      }
      p {
        font-weight: bolder;
      }
    }
  }
}
